import React, { useState } from 'react';
import debounce from 'lodash/debounce';
import { prop, sortBy } from 'ramda';

const Activity = ({
  setScreen,
  selectedActivity,
  selectActivity,
  activities,
  updateActivity,
  setActivities,
  tags: allTags,
}) => {
  const {
    id,
    code,
    title,
    synonyms,
    youtubeUrl,
    description,
    tags,
  } = selectedActivity;
  const [youtubeId, setYoutubeId] = useState(youtubeUrl);
  const [illustrationTitle, setIllustrationTitle] = useState('');
  const [illustrationUrl, setIllustrationUrl] = useState('');
  const [teaserTitle, setTeaserTitle] = useState('');
  const [teaserUrl, setTeaserUrl] = useState('');
  const [serviceTitle, setServiceTitle] = useState('');
  const [serviceUrl, setServiceUrl] = useState('');
  return (
    <div className="screen">
      <button onClick={() => setScreen(1)}>&larr; Назад</button>

      <h1>{title} ({code}, id={id})</h1>

      <div>
        <button onClick={() => selectActivity({
          ...activities[id - 1],
          id: id - 1,
        })} disabled={id === 0}>&larr;</button>
        <button onClick={() => selectActivity({
          ...activities[id + 1],
          id: id + 1,
        })} disabled={id === activities.length - 1}>&rarr;</button>
      </div>

      <div>
        <label>
          <h2>Синонимы</h2>
          <input type="text" size={100} defaultValue={synonyms} onChange={debounce(({ target }) => {
            selectedActivity.synonyms = target.value;
            updateActivity(selectedActivity, id);
            setActivities([...activities]);
          }, 1000)} />
        </label>
      </div>

      <div>
        <label>
          <h2>Родительская активность</h2>
          <select style={{...(selectedActivity.parent ? {} : {color: '#666'})}} defaultValue="null" value={selectedActivity.parent || 'null'} onChange={({ target }) => {
            selectedActivity.parent = target.value;
            updateActivity(selectedActivity, id);
            setActivities([...activities]);
          }}>
            <option hidden disabled value="null">выберите активность</option>
            {activities
              .filter((activity) => activity.code && activity.code !== '-')
              .sort((a, b) => a.title > b.title ? 1 : -1)
              .map((activity, index) => <option key={`${activity.code}${index}`} value={activity.code}>{activity.title}</option>)
            }
          </select>
        </label>
      </div>

      <div>
        <label>
          <h2>Описание</h2>
          <textarea cols="100" rows="10" defaultValue={description} onChange={debounce(({ target }) => {
            selectedActivity.description = target.value;
            updateActivity(selectedActivity, id);
            setActivities([...activities]);
          }, 1000)} />
        </label>
      </div>

      <div>
        <h2>Презентация</h2>
        <label>
          <h3>Код видео на YouTube</h3>
          <input type="text" value={youtubeId} onChange={({ target }) => {
            const youtubeId = target.value.split('=').pop();
            selectedActivity.youtubeUrl = youtubeId;
            setYoutubeId(youtubeId);
            updateActivity(selectedActivity, id);
            setActivities([...activities]);
          }} />
        </label>
        {youtubeUrl && (
          <div>
            <iframe width="400" height="200" src={'https://www.youtube.com/embed/' + youtubeUrl} title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
          </div>
        )}

        <h3>Иллюстрации</h3>
        <ul className="image-list">
          {selectedActivity.illustrations?.map(({ title, url }, index) => <li>
            <a href={url} target="_blank" rel="noreferrer"><img width={200} src={url} alt={title}/></a>
            <span className="remove-button" onClick={() => {
              if (!window.confirm(`Точно удалить иллюстрацию ${title || url}?`)) {
                return;
              }
              selectedActivity.illustrations = selectedActivity.illustrations.filter((_, i) => i !== index);
              updateActivity(selectedActivity, id);
              setActivities([...activities]);
            }}>🗑</span>
          </li>)}
        </ul>
        <input type="text" value={illustrationTitle} onChange={({ target: { value } }) => setIllustrationTitle(value)} placeholder="Название иллюстрации" />
        <input type="text" value={illustrationUrl} onChange={({ target: { value } }) => setIllustrationUrl(value)} placeholder="Ссылка на иллюстрацию" />
        <button onClick={() => {
          if (illustrationUrl === '') {
            return;
          }
          const illustrations = selectedActivity.illustrations || [];
          illustrations.push({
            title: illustrationTitle,
            url: illustrationUrl,
          });
          selectedActivity.illustrations = illustrations;
          updateActivity(selectedActivity, id);
          setActivities([...activities]);
          setIllustrationTitle('');
          setIllustrationUrl('');
        }}>Добавить иллюстрацию</button>

        <h3>Тизеры</h3>
        <ul>
          {selectedActivity.teasers?.map(({ title, url }, index) => <li>
            {url
              ? <a href={url} target="_blank" rel="noreferrer">{title || url}</a>
              : title || url
            }
            <span className="remove-button" onClick={() => {
              if (!window.confirm(`Точно удалить тизер ${title || url}?`)) {
                return;
              }
              selectedActivity.teasers = selectedActivity.teasers.filter((_, i) => i !== index);
              updateActivity(selectedActivity, id);
              setActivities([...activities]);
            }}>🗑</span>
          </li>)}
        </ul>
        <input type="text" value={teaserTitle} onChange={({ target: { value } }) => setTeaserTitle(value)} placeholder="Название тизера" />
        <input type="text" value={teaserUrl} onChange={({ target: { value } }) => setTeaserUrl(value)} placeholder="Ссылка на тизер" />
        <button onClick={() => {
          if (teaserTitle === '' && teaserUrl === '') {
            return;
          }
          const teasers = selectedActivity.teasers || [];
          teasers.push({
            title: teaserTitle,
            url: teaserUrl,
          });
          selectedActivity.teasers = teasers;
          updateActivity(selectedActivity, id);
          setActivities([...activities]);
          setTeaserTitle('');
          setTeaserUrl('');
        }}>Добавить тизер</button>
      </div>

      <div>
        <h2>Сервисы</h2>
        <ul>
          {selectedActivity.services?.map(({ title, url }, index) => <li>
            {url
              ? <a href={url} target="_blank" rel="noreferrer">{title || url}</a>
              : title || url
            }
            <span className="remove-button" onClick={() => {
              if (!window.confirm(`Точно удалить сервис ${title || url}?`)) {
                return;
              }
              selectedActivity.services = selectedActivity.services.filter((_, i) => i !== index);
              updateActivity(selectedActivity, id);
              setActivities([...activities]);
            }}>🗑</span>
          </li>)}
        </ul>
        <input type="text" value={serviceTitle} onChange={({ target: { value } }) => setServiceTitle(value)} placeholder="Название сервиса" />
        <input type="text" value={serviceUrl} onChange={({ target: { value } }) => setServiceUrl(value)} placeholder="Ссылка на сервис" />
        <button onClick={() => {
          if (serviceTitle === '' && serviceUrl === '') {
            return;
          }
          const services = selectedActivity.services || [];
          services.push({
            title: serviceTitle,
            url: serviceUrl,
          });
          selectedActivity.services = services;
          updateActivity(selectedActivity, id);
          setActivities([...activities]);
          setServiceTitle('');
          setServiceUrl('');
        }}>Добавить сервис</button>
      </div>

      <h2>Категории</h2>
      {sortBy(prop('title'), allTags).map((tag, index) => (
        <label className="tag" key={`${index}${tag.code}`}>
          <input type="checkbox" checked={(tags || []).includes(tag.code)} onChange={() => {
            let newTags = tags || [];
            if (newTags.includes(tag.code)) {
              newTags = newTags.filter(t => t !== tag.code);
            } else {
              newTags.push(tag.code);
            }
            selectedActivity.tags = newTags;
            updateActivity(selectedActivity, id);
            setActivities([...activities]);
          }} />
          {tag.title}
        </label>
      ))}
    </div>
  );
};

export default Activity;
